.ResultPage {
  background-color: rgb(248, 253, 251);
  color: rgb(0, 0, 0);
  will-change: transform, opacity;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: all 0.8s ease-out;
}

.ResultPage.visible {
  opacity: 1;
  visibility: visible;
}

.ResultImage {
  width: 100%;
  max-width: 400px;
  height: 300px;
  position: relative;
  margin-bottom: 30px;
  animation: slideInFromTop 1s ease-out 0.2s both;
}

@keyframes slideInFromTop {
  from { opacity: 0; transform: translateY(-50px) scale(0.9); }
  to { opacity: 1; transform: translateY(0) scale(1); }
}

.ResultImage .imgHover {
  background-image: linear-gradient(rgba(248, 253, 251, 0) 0%, rgba(248, 253, 251, 0) 80%, rgb(248, 253, 251) 100%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  border-radius: 20px;
}

.ResultImage .image {
  background-image: url("https://doda-static.com/img/1200/65/1/1/1752738441965-5492357.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(60, 217, 134, 0.2);
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

.ResultCard {
  background-color: rgba(248, 253, 251, 0.8);
  box-shadow: rgba(60, 217, 134, 0.19) 0px 5px 15px 0px;
  opacity: 1;
  visibility: visible;
  max-width: 500px;
  width: 100%;
  padding: 30px;
  border-radius: 20px;
  margin-bottom: 30px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(60, 217, 134, 0.1);
  animation: cardSlideUp 0.8s ease-out 0.4s both;
}

@keyframes cardSlideUp {
  from { opacity: 0; transform: translateY(50px) scale(0.95); }
  to { opacity: 1; transform: translateY(0) scale(1); }
}

.ResultCardTitle {
  text-align: center;
  margin-bottom: 20px;
}

.ResultCardTitle .smallTitle {
  font-size: 18px;
  color: rgb(0, 0, 0);
  margin-bottom: 10px;
  opacity: 0.8;
  animation: typewriter 1s ease-out 0.6s both;
}

@keyframes typewriter {
  from { width: 0; opacity: 0; }
  to { width: 100%; opacity: 0.8; }
}

.ResultCardTitle .mainTitle {
  color: rgb(60, 217, 134);
  font-size: 28px;
  font-weight: 700;
  animation: glowPulse 2s ease-in-out infinite;
}

@keyframes glowPulse {
  0%, 100% { text-shadow: 0 0 10px rgba(60, 217, 134, 0.3); }
  50% { text-shadow: 0 0 20px rgba(60, 217, 134, 0.6); }
}

.Line {
  height: 5px;
  margin: 20px 0px;
  display: flex;
  justify-content: center;
  animation: lineExpand 0.8s ease-out 0.8s both;
}

@keyframes lineExpand {
  from { transform: scaleX(0); }
  to { transform: scaleX(1); }
}

.Line .box {
  width: 30px;
  background-color: rgba(60, 217, 134, 0.25);
  height: 100%;
  border-radius: 3px;
}

.ResultCardText {
  color: rgb(0, 0, 0);
  text-align: center;
  line-height: 1.6;
  animation: fadeInText 1s ease-out 1s both;
}

@keyframes fadeInText {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.ResultCardText p {
  margin: 10px 0;
  font-size: 16px;
}

.Space {
  height: 10px;
  width: 100%;
  margin-top: 0px;
  margin-bottom: 0px;
}

.ResultPage .btnContainer {
  margin: 10px 0;
  width: 100%;
  display: flex;
  justify-content: center;
  animation: buttonSlideIn 0.6s ease-out both;
}

.ResultPage .btnContainer:nth-child(4) { animation-delay: 1.2s; }
.ResultPage .btnContainer:nth-child(5) { animation-delay: 1.4s; }

@keyframes buttonSlideIn {
  from { opacity: 0; transform: translateX(-30px); }
  to { opacity: 1; transform: translateX(0); }
}

.ResultPage .Button {
  background: rgb(60, 217, 134);
  box-shadow: rgba(60, 217, 134, 0.34) 0px 5px 20px;
  width: 70%;
  max-width: 300px;
  transition: all 0.3s ease-in-out;
  border-radius: 50px;
  padding: 16px 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: none;
  outline: none;
  position: relative;
  overflow: hidden;
}

.ResultPage .Button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: rgba(60, 217, 134, 0.5) 0px 10px 30px;
}

.ResultPage .Button:active {
  transform: translateY(-1px) scale(0.98);
}

.ResultPage .Button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s ease;
}

.ResultPage .Button:hover::before {
  left: 100%;
}

.ResultPage .btnIcon {
  display: flex;
  align-items: center;
}

.ResultPage .btnIcon .material-icons {
  color: rgb(255, 255, 255);
  font-size: 24px;
  margin-right: 10px;
  animation: iconBounce 2s ease-in-out infinite;
}

@keyframes iconBounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-3px); }
  60% { transform: translateY(-1px); }
}

.ResultPage .btnText {
  font-size: 16px;
  color: rgb(255, 255, 255);
  font-weight: 500;
  min-width: 130px;
  padding: 6px;
  text-align: center;
}

/* Î∞òÏùëÌòï ÎîîÏûêÏù∏ */
@media (max-width: 768px) {
  .ResultCard {
    padding: 25px;
    margin-bottom: 25px;
  }
  
  .ResultCardTitle .mainTitle {
    font-size: 24px;
  }
  
  .ResultCardTitle .smallTitle {
    font-size: 16px;
  }
  
  .ResultImage {
    height: 250px;
    margin-bottom: 25px;
  }
  
  .ResultPage .Button {
    width: 80%;
  }
}

@media (max-width: 480px) {
  .ResultCard {
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .ResultCardTitle .mainTitle {
    font-size: 22px;
  }
  
  .ResultCardTitle .smallTitle {
    font-size: 14px;
  }
  
  .ResultImage {
    height: 200px;
    margin-bottom: 20px;
  }
  
  .ResultPage .Button {
    width: 85%;
  }
  
  .ResultCardText p {
    font-size: 14px;
  }
}

