.ResultPage.visible .ResultCard {
  animation: resultCardPulse 3s ease-in-out infinite;
}

@keyframes resultCardPulse {
  0%, 100% { 
    box-shadow: rgba(60, 217, 134, 0.19) 0px 5px 15px 0px;
  }
  50% { 
    box-shadow: rgba(60, 217, 134, 0.35) 0px 8px 25px 0px;
  }
} 