.QuizPage {
  background-color: rgb(248, 253, 251);
  color: rgb(0, 0, 0);
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  animation: fadeIn 0.5s ease-in-out;
  overflow: hidden;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.QuizPage .Logo {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.QuizPage .logoImage {
  height: 80px;
  width: 180px;
  background-image: url("https://doda-static.com/img/600/65/1/1/1752736751640-1984109.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.QuizProgressText {
  color: rgba(0, 0, 0, 0.3);
  text-align: center;
  font-size: 16px;
  margin-top: 120px;
  margin-bottom: 20px;
  animation: slideInDown 0.6s ease-out;
}

@keyframes slideInDown {
  from { opacity: 0; transform: translateY(-30px); }
  to { opacity: 1; transform: translateY(0); }
}

.QuizProgressText .now {
  font-weight: 700;
  color: rgb(60, 217, 134);
  font-size: 18px;
}

.QuizProgressText .total {
  font-weight: 400;
  font-size: 18px;
}

.ProgressBar {
  width: 90%;
  max-width: 400px;
  margin: 0 auto 40px;
  display: flex;
  align-items: center;
  gap: 15px;
  animation: slideInLeft 0.7s ease-out;
}

@keyframes slideInLeft {
  from { opacity: 0; transform: translateX(-50px); }
  to { opacity: 1; transform: translateX(0); }
}

.ProgressBar .back {
  opacity: 0.6;
  background-color: rgb(60, 217, 134);
  color: rgb(255, 255, 255);
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  user-select: none;
  font-size: 18px;
  flex-shrink: 0;
}

.ProgressBar .back:hover {
  opacity: 1;
  transform: scale(1.1);
  box-shadow: 0 4px 15px rgba(60, 217, 134, 0.3);
}

.ProgressBg {
  background-color: rgba(0, 0, 0, 0.07);
  height: 8px;
  border-radius: 4px;
  flex: 1;
  overflow: hidden;
}

.Progress {
  height: 8px;
  background-color: rgb(60, 217, 134);
  border-radius: 4px;
  transition: width 0.5s ease-in-out;
  box-shadow: 0 2px 10px rgba(60, 217, 134, 0.3);
}

.QuizContents {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.questionContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 500px;
  margin: 0 auto;
  width: 100%;
  position: relative;
}

.QuizCount {
  margin-bottom: 30px;
  animation: bounceIn 0.8s ease-out;
}

@keyframes bounceIn {
  0% { transform: scale(0.3); opacity: 0; }
  50% { transform: scale(1.05); }
  70% { transform: scale(0.9); }
  100% { transform: scale(1); opacity: 1; }
}

.QuizCount .text {
  color: rgb(60, 217, 134);
  font-size: 48px;
  font-weight: 700;
  text-align: center;
}

.textContainer {
  margin-bottom: 50px;
  animation: fadeInScale 0.7s ease-out 0.2s both;
}

@keyframes fadeInScale {
  from { opacity: 0; transform: scale(0.8); }
  to { opacity: 1; transform: scale(1); }
}

.QuizText {
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  color: rgb(0, 0, 0);
  line-height: 1.4;
}

.buttons {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
  position: relative;
  min-height: 280px; /* Î≤ÑÌäºÎì§Ïùò ÏµúÏÜå ÎÜíÏù¥Î•º ÌôïÎ≥¥ÌïòÏó¨ Î†àÏù¥ÏïÑÏõÉ ÏïàÏ†ïÏÑ± Ïú†ÏßÄ */
}

/* ÏÉà Î≤ÑÌäºÎì§Ïù¥ Ïä¨ÎùºÏù¥Îìú Ïù∏Ìï† Îïå */
.buttons.slide-in .btnContainer {
  opacity: 0;
  transform: translateX(100px);
}

.btnContainer {
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* Î≤ÑÌäº Ïä¨ÎùºÏù¥Îìú Ïù∏ Ïï†ÎãàÎ©îÏù¥ÏÖò (Ïò§Î•∏Ï™ΩÏóêÏÑú Îì±Ïû•) */
.btnContainer.sliding-in {
  animation: slideInFromRight 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@keyframes slideInFromRight {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Î≤ÑÌäº Ïä¨ÎùºÏù¥Îìú ÏïÑÏõÉ Ïï†ÎãàÎ©îÏù¥ÏÖò (ÏôºÏ™ΩÏúºÎ°ú Ï¥§ÎùºÎùΩ) */
.btnContainer.sliding-out {
  animation: slideOutToLeft 0.4s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
}

@keyframes slideOutToLeft {
  from {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
  to {
    opacity: 0;
    transform: translateX(-120px) scale(0.8);
  }
}

.buttons .Button {
  background: rgb(60, 217, 134);
  box-shadow: rgba(60, 217, 134, 0.34) 0px 5px 20px;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  transition: all 0.3s ease-in-out;
  border-radius: 25px;
  padding: 20px 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: none;
  outline: none;
  position: relative;
  overflow: hidden;
  min-height: 60px;
}

.buttons .Button:hover {
  transform: translateY(-3px) scale(1.02);
  box-shadow: rgba(60, 217, 134, 0.5) 0px 8px 25px;
}

.buttons .Button:active {
  transform: translateY(-1px) scale(0.98);
}

.buttons .Button.selected {
  background: rgb(45, 180, 110);
  transform: scale(1.05);
  box-shadow: rgba(60, 217, 134, 0.6) 0px 10px 30px;
  animation: selectedPulse 0.3s ease-out;
}

@keyframes selectedPulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1.05); }
}

.buttons .Button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.buttons .Button:hover::before {
  left: 100%;
}

.buttons .btnIcon {
  display: none;
}

.buttons .btnText {
  font-size: 16px;
  color: rgb(255, 255, 255);
  font-weight: 500;
  text-align: center;
  line-height: 1.4;
  white-space: pre-line;
}

/* Ïï†ÎãàÎ©îÏù¥ÏÖò Ï§ë Î≤ÑÌäº ÏÉÅÌò∏ÏûëÏö© Ï†úÏñ¥ */
.buttons.slide-out .Button {
  pointer-events: none;
}

/* Î∞òÏùëÌòï ÎîîÏûêÏù∏ */
@media (max-width: 768px) {
  .QuizProgressText {
    margin-top: 100px;
    font-size: 14px;
  }
  
  .QuizProgressText .now,
  .QuizProgressText .total {
    font-size: 16px;
  }
  
  .QuizCount .text {
    font-size: 40px;
  }
  
  .QuizText {
    font-size: 22px;
  }
  
  .buttons {
    min-height: 260px; /* ÌÉúÎ∏îÎ¶øÏóêÏÑúÎèÑ Î†àÏù¥ÏïÑÏõÉ ÏïàÏ†ïÏÑ± Ïú†ÏßÄ */
  }
  
  .buttons .Button {
    padding: 18px 20px;
    min-height: 55px;
  }
  
  .buttons .btnText {
    font-size: 15px;
  }
  
  .ProgressBar {
    width: 85%;
  }

  .QuizPage .logoImage {
    height: 70px;
    width: 160px;
  }
}

@media (max-width: 480px) {
  .QuizProgressText {
    margin-top: 80px;
    font-size: 12px;
  }
  
  .QuizProgressText .now,
  .QuizProgressText .total {
    font-size: 14px;
  }
  
  .QuizCount .text {
    font-size: 36px;
  }
  
  .QuizText {
    font-size: 20px;
  }
  
  .buttons {
    min-height: 240px; /* Î™®Î∞îÏùºÏóêÏÑúÎèÑ Î†àÏù¥ÏïÑÏõÉ ÏïàÏ†ïÏÑ± Ïú†ÏßÄ */
  }
  
  .buttons .Button {
    padding: 16px 18px;
    min-height: 50px;
    border-radius: 20px;
  }
  
  .buttons .btnText {
    font-size: 14px;
  }
  
  .ProgressBar {
    width: 90%;
  }

  .ProgressBar .back {
    width: 28px;
    height: 28px;
    font-size: 16px;
  }

  .QuizPage .logoImage {
    height: 60px;
    width: 140px;
  }

  .QuizPage .Logo {
    top: 20px;
  }

  /* Î™®Î∞îÏùºÏóêÏÑú Ïä¨ÎùºÏù¥Îìú Í±∞Î¶¨ Ï°∞Ï†ï */
  @keyframes slideOutToLeft {
    from {
      opacity: 1;
      transform: translateX(0) scale(1);
    }
    to {
      opacity: 0;
      transform: translateX(-100px) scale(0.8);
    }
  }
} 